<template>
  <section>
    <el-row>
      <el-col :span="4">
        <span class="top-nav-link" @click="goBack()">返回上一页</span>
      </el-col>
    </el-row>
    <el-row class="border-box">
      <el-col align="center">
        <span class="box-title">论治结果</span>
      </el-col>
      <el-col style="margin-top: 8px;">
        <el-table ref="yfTable" @select="handelYaoFangTableSelection" :data="lzData">
          <el-table-column type="selection"></el-table-column>
          <el-table-column label="症型" prop="dialecticalName"></el-table-column>
          <el-table-column label="治法" prop="cureName"></el-table-column>
          <el-table-column label="方剂名称" prop="prescriptionName"></el-table-column>
          <el-table-column label="推荐" prop="subID"></el-table-column>
          <el-table-column label="方解">
            <!-- 药解弹窗 -->
            <template slot-scope="scope">
              <el-popover placement="left-end" width="420" trigger="hover">
                <div>
                  <div>【方解】 {{fangjie.calcite}}</div>
                  <div>【来源】 {{fangjie.source}}</div>
                  <div>【功效】 {{fangjie.effect}}</div>
                  <div>【主治】 {{fangjie.attend}}</div>
                </div>
                <img
                  @mouseover="handelGetFangJie(scope.row)"
                  slot="reference"
                  src="../../assets/imgs/explain.png"
                  class="chufang-icon"
                >
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row style="margin-top: 20px">
      <el-col :span="14">
        <!-- 上部穴位 -->
        <el-row class="zj-xuewei">
          <el-col :span="12">
            <span class="active" @click="handelSelectTag(1,$event)">穴位</span>
            <span @click="handelSelectTag(2,$event)">开穴</span>
            <span @click="handelSelectTag(3,$event)">闭穴</span>
          </el-col>
          <el-col :span="11" align="right">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-plus"
              @click="handelDialogAddxuewei"
            >添加</el-button>
          </el-col>
          <el-col :span="24" style="margin-top: 8px">
            <el-table :data="xueweiListData" height="368">
              <el-table-column type="index" width="40"></el-table-column>
              <el-table-column prop="acupointName" width="80px" label="穴位"></el-table-column>
              <el-table-column prop="positionExplain" min-width="240px" label="穴位说明"></el-table-column>
              <el-table-column label="穴解" width="80px">
                <template slot-scope="scope">
                  <el-popover placement="right-end" width="420" trigger="hover">
                    <div>
                      <div>【穴位说明】 {{xuejieObj.positionExplain}}</div>
                      <div>【X坐标】 {{xuejieObj.pointX}}</div>
                      <div>【Y坐标】 {{xuejieObj.pointY}}</div>
                      <div>【所属经络】{{xuejieObj.meridianName}}</div>
                      <div>【在经络上的顺序】{{xuejieObj.meridianOrder}}</div>
                    </div>
                    <img
                      @mouseover="handelGetXueJie(scope.row)"
                      slot="reference"
                      src="../../assets/imgs/explain.png"
                      class="chufang-icon"
                    >
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column prop="name" label="操作">
                <template slot-scope="scope">
                  <el-button
                    v-if="isShowRemmove"
                    @click="handelRemoveXue(scope.$index)"
                    type="danger"
                    size="mini"
                    icon="el-icon-delete"
                  ></el-button>
                  <el-button
                    v-show="isShowAdd"
                    @click="handelAddXue(scope.$index,scope.row)"
                    type="danger"
                    size="mini"
                    icon="el-icon-plus"
                    circle
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
        <el-row style="margin-top:16px;font-size: 16px">
          <el-col :span="4" class="time-bar bar-left">
            <span>开穴时间</span>
          </el-col>
          <el-col :span="20" class="time-bar bar-right">{{kauxueTime.effectiveTime}}</el-col>
          <el-col :span="24" style="margin-top:16px">
            <el-progress
              :text-inside="true"
              :stroke-width="24"
              :percentage="kauxueTime.timebar"
              color="rgba(233, 190, 147, 1)"
            ></el-progress>
          </el-col>
          <el-col :span="24" style="margin-top:16px;line-height:36px">
            <el-col :span="4">留针时间</el-col>
            <el-col :span="4">
              <el-input v-model="zjMinutes"></el-input>
            </el-col>
            <el-col :span="4">&nbsp;&nbsp;分钟</el-col>
          </el-col>
          <el-col style="margin-top:22px">
            <el-form label-width="80px">
              <el-form-item label="西医病名">
                <el-input readonly v-model="westernMedicineName" placeholder="请选择西医名称">
                  <el-button slot="append" icon="el-icon-search" @click="handelSelectXY"></el-button>
                </el-input>
              </el-form-item>
              <el-form-item label="中医病名">
                <el-input readonly v-model="chineseMedicineName" placeholder="请选择中医病名">
                  <el-button slot="append" icon="el-icon-search" @click="handelSelectZY"></el-button>
                </el-input>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-col>
      <el-col :offset="1" :span="9">
        <!-- 穴位图 canvas 绘制 -->
        <acupoin-img :dataList="xueweiListData"></acupoin-img>
      </el-col>
    </el-row>
    <el-row class="toolbar">
      <el-col :span="12" align="left">
        <el-button type="primary" @click="handelOpenjingyan">取经验方</el-button>
        <el-button type="primary" @click="handelSaveJingYan">存经验方</el-button>
      </el-col>
      <el-col :span="12" align="right">
        <el-button type="primary" @click="handelFinish(0)">保存就诊</el-button>
        <el-button type="primary" @click="handelFinish(1)">完成就诊</el-button>
      </el-col>
    </el-row>
    <jingyan-bar @jingyanCode="getJingYanCode" ref="getJingYan"></jingyan-bar>
    <el-dialog width="50%" title="添加穴位" center :visible.sync="dialogXueWei">
      <el-row>
        <el-col :offset="5" :span="14">
          <el-input
            suffix-icon="el-icon-search"
            v-model="searchTextWuewei"
            @keydown.enter.native="getAllAcupointList"
            placeholder="输入您想查询的穴位编码、名称、拼音码"
          ></el-input>
        </el-col>
        <el-col :span="24" style="margin-top: 10px">
          <el-table
            @cell-dblclick="handelDbclickAddxuewei"
            :data="xueweiAddListData"
            width="100%"
            height="500"
          >
            <el-table-column label="编码" prop="acupointCode"></el-table-column>
            <el-table-column label="名称" prop="acupointName"></el-table-column>
            <el-table-column label="拼音码" prop="pinYinCode"></el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-dialog>
    <!-- 弹出框 西药病名 -->
    <el-dialog :visible.sync="dialog_xiyi" width="60%">
      <el-row>
        <el-col :span="12" :offset="6">
          <el-input
            clearable
            v-model="searchXY"
            @blur="handelSearchXY"
            @keydown.enter.native="handelSearchXY"
            placeholder="请输入您想要查询的病名或拼音码"
            prefix-icon="el-icon-search"
          ></el-input>
        </el-col>
        <el-col style="margin-top: 26px">
          <el-table
            height="480"
            @row-dblclick="handelSelectXiYi"
            :data="xyData"
            style="width: 100%"
          >
            <el-table-column
              header-align="center"
              align="center"
              width="120"
              prop="westernMedicineCode"
              label="西医编码"
            ></el-table-column>
            <el-table-column
              header-align="center"
              align="center"
              prop="westernMedicineName"
              label="西医病名"
            ></el-table-column>
            <el-table-column header-align="center" align="center" prop="pinYinCode" label="拼音码"></el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-dialog>
    <!-- 中医病名 -->
    <el-dialog :visible.sync="dialog_chineseMedicine" width="60%">
      <el-row>
        <el-col :span="12" :offset="6">
          <el-input
            clearable
            v-model="searchZY"
            @blur="handelSearchZY"
            @keydown.enter.native="handelSearchZY"
            placeholder="请输入您想要查询的病名或拼音码"
            prefix-icon="el-icon-search"
          ></el-input>
        </el-col>
        <el-col style="margin-top: 26px">
          <el-table @row-dblclick="handelSelectZYi" :data="zyData" height="480" style="width: 100%">
            <el-table-column
              header-align="right"
              align="right"
              width="120"
              prop="chineseMedicineCode"
              label="中医编码"
            ></el-table-column>
            <el-table-column
              header-align="center"
              align="center"
              prop="chineseMedicineName"
              label="中医病名"
            ></el-table-column>
            <el-table-column header-align="center" align="center" prop="pinYinCode" label="拼音码"></el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-dialog>
  </section>
</template>
<script>
import {
  searchChineseMedicineList, // 中医病名列表
  getAllWesternMedicineName, // 西医病名列表
  getAllAcupoint, // 穴位列表
  getKaiXue, // 开穴
  getBiXue, // 闭穴
  getDrugSolution, // 获取穴解
  getEffectiveTime, // 有效时间
  wcjzsaveOrFinishZJ, // 完成就诊
  getGovernanceResultZJ, // 获取论证结果
  getAcupointByPrescriptionCode, // 论治详情
  getCalciteByPrescriptionCode // 方解
} from "../../api";
import acupoin_img from "../../components/acupoin_img";
import jingyan_bar from "../../components/jingyan_bar";
import { mapState } from "vuex";
export default {
  components: {
    "acupoin-img": acupoin_img,
    "jingyan-bar": jingyan_bar
  },
  computed: {
    ...mapState("caseNote", {
      pro_cureCode: state => state.cureCode,
      pro_dialecticalCode: state => state.dialecticalCode
    }),
    ...mapState("patient", {
      empBasicMainID: state => state.empBasicMainID,
      registrationNum: state => state.registrationNum
    }),
    xueweiListData: {
      get: function() {
        if (this.tagIndex === 1) {
          return [...this.xueweiData, ...this.zjItem];
        }
        if (this.tagIndex === 2) {
          return this.kaixueData;
        }
        if (this.tagIndex === 3) {
          return this.bixueData;
        }
      },
      set: function() {
        return this.xueweiListData;
      }
    },
    isShowAdd: function() {
      if (this.tagIndex == 2) {
        return true;
      } else {
        return false;
      }
    },
    isShowRemmove: function() {
      if (this.tagIndex == 1) {
        return true;
      } else {
        return false;
      }
    },
    canvasListData: function() {
      return this.xueweiData;
    }
  },
  data() {
    return {
      tagIndex: 1, // 标签切换标记
      xueweiData: [], // 穴位列表数据
      kaixueData: [], // 开穴列表数据
      bixueData: [], // 闭穴列表数据
      xuejieObj: {}, // 穴解模板
      dialogXueWei: false, // 穴位窗口
      xueweiAddListData: [], // 穴位(添加)列表
      searchTextWuewei: "", // 穴位搜索字段
      kauxueTime: { timer: null, jsTime: null, timebar: 100 }, // 开穴时间相关
      timer: null, // 定时器
      zjMinutes: "10", // 留针时间
      dialog_chineseMedicine: false, // 中医病名窗口
      dialog_xiyi: false, // 西医病名
      xyData: [], // 西医表格数据
      zyData: [], // 中医表格数据
      searchXY: "", // 查询西医
      searchZY: "", // 查询中医
      chineseMedicineName: "", //  中医名称
      chineseMedicineCode: "", //  中医编号
      westernMedicineName: "", //  西医名称
      westernMedicineCode: "", //  西医编号
      lzData: [], // 论治结果
      fangjie: {}, // 方解
      rowSign: {},
      zjItem: [] // 针灸方论治列表
    };
  },
  methods: {
    getJingYanCode(row) {
      // 获取经验组件code
      this.lzData.push(row);
      // getAcupointByPrescriptionCode({prescriptionCode: code}).then(res=>{
      //   this.xueweiData = res.data.data
      // })
    },
    handelOpenjingyan() {
      // 打开经验方接口
      this.$refs.getJingYan.jingyanOpenDialog();
    },
    handelSaveJingYan() {
      // 打开存经验方
      let data = [...this.xueweiData, ...this.zjItem];
      this.$refs.getJingYan.jingyanSaveJingYan(data);
    },
    handelYaoFangTableSelection(row, item) {
      // 选择药方
      this.$refs.yfTable.clearSelection(); // 单选处理
      if (this.rowSign === item) {
        this.rowSign = {};
        this.zjItem = [];
        return;
      }
      this.rowSign = item;
      this.$refs.yfTable.toggleRowSelection(item);
      getAcupointByPrescriptionCode({
        prescriptionCode: item.prescriptionCode
      }).then(res => {
        this.zjItem = res.data.data;
      });
    },
    handelGetFangJie(row) {
      // 获取方解
      if (this.fangjieActive == row.prescriptionCode) {
        return;
      }
      this.fangjie = [];
      this.fangjieActive = row.prescriptionCode;
      getCalciteByPrescriptionCode({
        prescriptionCode: row.prescriptionCode
      }).then(res => {
        if (res.data.code == "0") {
          this.fangjie = res.data.data[0];
        }
      });
    },
    handelSelectXY(){ // 打开西医窗口
      this.dialog_xiyi = true
      this.searchXY = ''
      this.handelSearchXY()
    },
    handelSearchXY(){ // 西医病名搜索查询
      getAllWesternMedicineName({
        searchText: this.searchXY
      }).then(res=>{
          this.xyData = res.data.data
      })
    },
    handelSelectXiYi(row) {
      // 双击选择西医
      this.dialog_xiyi = false;
      this.westernMedicineName = row.westernMedicineName;
      this.westernMedicineCode = row.westernMedicineCode;
    },
    handelSelectZYi(row) {
      // 双击选择中医
      this.chineseMedicineName = row.chineseMedicineName;
      this.chineseMedicineCode = row.chineseMedicineCode;
      this.dialog_chineseMedicine = false;
    },
    handelSelectZY(){ // 中医窗口
      this.dialog_chineseMedicine=true
      this.handelSearchZY()
    },
    handelSearchZY(){ // 中医病名搜索查询
      searchChineseMedicineList({
        searchText: this.searchZY
      }).then(res=>{
        this.zyData = res.data.data
      })
    },
    handelFinish(flag) {
      // 完成就诊
      if (this.xueweiListData.length == 0) {
        this.$message.error("穴位不能为空!");
        return;
      }
      wcjzsaveOrFinishZJ({
        empBasicMainID: this.empBasicMainID, // ID
        registrationNum: this.registrationNum,
        zjMinutes: this.zjMinutes, // 留针时间
        fangJiDetailList: this.xueweiListData,
        westernMedicineCode: this.westernMedicineCode,
        chineseMedicineCode: this.chineseMedicineCode,
        flag: flag
      }).then(res => {
        if (res.data.code === "0") {
          this.$message.success("保存成功!");
          if(flag=='1'){
             // 完成就诊 跳转到列表页
             this.$router.push('list')
           }
        } else {
          this.$message.error("保存失败!");
        }
      });
    },
    handelSelectTag(index, e) {
      // 上部穴位切换
      this.tagIndex = index;
      document.querySelector(".zj-xuewei .active").className = "";
      e.target.className = "active";
    },
    handelGetXueJie(row) {
      // 获取穴解 acupointCode
      if (row.acupointCode == this.xuejieObj.acupointCode) {
        return;
      }
      getDrugSolution({ acupointCode: row.acupointCode }).then(res => {
        this.xuejieObj = res.data.data[0];
      });
    },
    handelRemoveXue(index, row) {
      // 移除列表穴位条目
      if (index < this.xueweiData.length && this.xueweiData.length != 0) {
        this.xueweiData.splice(index, 1);
      } else {
        this.zjItem.splice(index - this.xueweiData.length, 1);
      }
    },
    handelAddXue(index, row) {
      // 开穴添加到穴位
      this.$message.success(`${row.acupointName} 穴位添加成功！`);
      this.xueweiData.push(row);
    },
    handelDialogAddxuewei() {
      // 添加穴位
      this.dialogXueWei = true;
      this.getAllAcupointList();
    },
    getAllAcupointList() {
      // 获取穴位列表
      getAllAcupoint({ searchText: this.searchTextWuewei }).then(res => {
        if (res.data.code === "0") {
          this.xueweiAddListData = res.data.data;
        }
      });
    },
    handelDbclickAddxuewei(row, column) {
      // 双击添加穴位
      if (
        this.xueweiData.find(element => {
          return row === element;
        })
      ) {
        this.$message.warning(`穴位[${row.acupointName}]已存在列表!`);
        return;
      }
      this.xueweiData.push(row);
      this.$message.success(`${row.acupointName} 穴位添加成功！` )
    },
    getInitListData() {
      // 获取初始数据
      getKaiXue().then(res => {
        if (res.data.code == "0") this.kaixueData = res.data.data;
      });
      getBiXue().then(res => {
        if (res.data.code == "0") this.bixueData = res.data.data;
      });
      getEffectiveTime().then(res => {
        // 开穴时间处理
        if (res.data.code == "0") {
          this.kauxueTime = { ...res.data.data, ...this.kauxueTime };
          this.timerKaixueEnd();
        }
      });
    },
    timerKaixueEnd() {
      // 开穴时间 倒计时
      this.timer = setInterval(() => {
        if (this.kauxueTime.timebar <= 0) {
          window.clearInterval(this.timer);
        } else {
          this.kauxueTime.timebar = parseInt(
            this.kauxueTime.timebar - this.kauxueTime.txtPointIfOpenEndTime / 100
          );
        }
      }, 1000 * 60);
    }
  },
  mounted() {
    this.getInitListData(); // 初始穴位
    getGovernanceResultZJ({
      // 获取论治结果
      empBasicMainID: this.empBasicMainID,
      operationType: 2, // 症型治法,
      dialecticalCode: this.pro_dialecticalCode,
      cureCode: this.pro_cureCode
    }).then(res => {
      if (res.data.code === "0") {
        this.lzData = res.data.data;
      }
    });
  }
};
</script>
<style scoped>
.zj-xuewei {
  margin-top: 4px;
}
.zj-xuewei span {
  font-size: 16px;
  color: #999999;
  margin: 0 14px;
  cursor: pointer;
}
.zj-xuewei span:hover {
  color: #bf7045;
}
.zj-xuewei .active {
  border-bottom: 1px solid #bf7045;
  color: #bf7045;
}
.time-bar {
  height: 32px;
  line-height: 32px;
}
.bar-left {
  font-size: 16px;
}
.bar-right {
  padding-left: 20px;
  font-size: 14px;
  background: #efe7e6;
}
</style>



